import React from 'react'
import { Link } from 'react-router-dom';
export default function Navigation() {
  return (
    <aside
      class="sidenav bg-white navbar navbar-vertical navbar-expand-xs border-0 border-radius-xl my-3 fixed-start ms-4"
      id="sidenav-main"
    >
      <div class="sidenav-header">
        <i
          class="fas fa-times p-3 cursor-pointer text-secondary opacity-5 position-absolute end-0 top-0 d-none d-xl-none"
          aria-hidden="true"
          id="iconSidenav"
        ></i>
        <Link
          class="navbar-brand m-0"
          to="/"
        >
          <img
            src="./assets/img/logo-ct-dark.png"
            class="navbar-brand-img h-100"
            alt="main_logo"
          />

          <span class="ms-1 font-weight-bold">Patient System</span>
        </Link>
      </div>
      <hr class="horizontal dark mt-0" />

      <div class="collapse navbar-collapse w-auto" id="sidenav-collapse-main">
        <ul class="navbar-nav">
          <li class="nav-item">
            <Link class="nav-link" to="/">
              <div
                class="icon icon-shape icon-sm border-radius-md text-center me-2 d-flex align-items-center justify-content-center"
              >
                <i class="ni ni-tv-2 text-primary text-sm opacity-10"></i>
              </div>

              <span class="nav-link-text ms-1">Dashboard</span>
            </Link>
          </li>

          <li class="nav-item">
            <Link class="nav-link" to="/NewPatient">
              <div
                class="icon icon-shape icon-sm border-radius-md text-center me-2 d-flex align-items-center justify-content-center"
              >
                <i
                  class="fa far fa-user-circle text-warning text-sm opacity-10"
                ></i>
              </div>

              <span class="nav-link-text ms-1">New Patient</span>
            </Link>
          </li>

          <li class="nav-item">
            <Link class="nav-link" to="/PatientList">
              <div
                class="icon icon-shape icon-sm border-radius-md text-center me-2 d-flex align-items-center justify-content-center"
              >
                <i
                  class="fa fa-list text-dark text-sm opacity-10"
                ></i>
              </div>

              <span class="nav-link-text ms-1">  Patient List</span>
            </Link>
          </li>
          <li class="nav-item">
            <Link class="nav-link" to="/TodaysPatient">
              <div
                class="icon icon-shape icon-sm border-radius-md text-center me-2 d-flex align-items-center justify-content-center"
              >
                <i
                  class="fa fa-users text-success text-sm opacity-10"
                ></i>
              </div>

              <span class="nav-link-text ms-1">Today's Patient</span>
            </Link>
          </li>

          <li class="nav-item">
            <Link class="nav-link" to="./Drugs">
              <div
                class="icon icon-shape icon-sm border-radius-md text-center me-2 d-flex align-items-center justify-content-center"
              >
                <i class="fas fa-pump-medical text-info text-sm opacity-10"></i>
              </div>

              <span class="nav-link-text ms-1">Drugs</span>
            </Link>
          </li>

          <li class="nav-item">
            <Link class="nav-link" to="./Search">
              <div
                class="icon icon-shape icon-sm border-radius-md text-center me-2 d-flex align-items-center justify-content-center"
              >
                <i class="fa fa-search text-danger text-sm opacity-10"></i>
              </div>

              <span class="nav-link-text ms-1">Search</span>
            </Link>
          </li>

          {/* <!-- <li class="nav-item mt-3">
            <h6
              class="ps-4 ms-2 text-uppercase text-xs font-weight-bolder opacity-6"
            >
              Account pages
            </h6>
          </li> --> */}

          <li class="nav-item">
            <Link class="nav-link" to="./Profile">
              <div
                class="icon icon-shape icon-sm border-radius-md text-center me-2 d-flex align-items-center justify-content-center"
              >
                <i class="ni ni-single-02 text-dark text-sm opacity-10"></i>
              </div>

              <span class="nav-link-text ms-1">Profile</span>
            </Link>
          </li>

          <li class="nav-item">
            <Link class="nav-link" to="./Login">
              <div
                class="icon icon-shape icon-sm border-radius-md text-center me-2 d-flex align-items-center justify-content-center"
              >
                <i
                  class="ni ni-single-copy-04 text-warning text-sm opacity-10"
                ></i>
              </div>

              <span class="nav-link-text ms-1">Sign In</span>
            </Link>
          </li>

          <li class="nav-item">
            <Link class="nav-link" to="./Register">
              <div
                class="icon icon-shape icon-sm border-radius-md text-center me-2 d-flex align-items-center justify-content-center"
              >
                <i class="ni ni-collection text-info text-sm opacity-10"></i>
              </div>

              <span class="nav-link-text ms-1">Sign Up</span>
            </Link>
          </li>
        </ul>
      </div>

      {/* <!-- <div class="sidenav-footer mx-3">
        <div class="card card-plain shadow-none" id="sidenavCard">
          <img
            class="w-50 mx-auto"
            src="./assets/img/illustrations/icon-documentation.svg"
            alt="sidebar_illustration"
          />
          <div class="card-body text-center p-3 w-100 pt-0">
            <div class="docs-info">
              <h6 class="mb-0">Need help?</h6>
              <p class="text-xs font-weight-bold mb-0">Please check our docs</p>
            </div>
          </div>
        </div> --> */}

        {/* <!-- <Link
          to="https://www.creative-tim.com/learning-lab/bootstrap/license/argon-dashboard"
          target="_blank"
          class="btn btn-dark btn-sm w-100 mb-3"
          >Documentation</Link
        >
        <Link
          class="btn btn-primary btn-sm mb-0 w-100"
          to="https://www.creative-tim.com/product/argon-dashboard-pro?ref=sidebarfree"
          type="button"
          >Upgrade to pro</Link
        > --> */}
      {/* </div> */}
    </aside>
  );
}
